export default {
	data() {
		return {
			account:null,
			pwd:null
		}
	},
	onLoad() {

	},
	methods: {
		doLogin(){
			uni.request({
				url:this.prefix + "/doLogin",
				data:{
					maccount: this.account,
					mpwd: this.pwd,
					loginType: "wxapp"
				},
				success: (res) => {
					if(res.data.code == 200){
						uni.showToast({
							title:"认证成功",
							icon:"none"
						})
						uni.setStorageSync("LOGIN_MERCHANT",res.data.data)
						setTimeout(()=>{
							uni.switchTab({
								url:"../tabbar/data"
							})
						},800)
					}else{
						uni.showToast({
							title:"认证失败",
							icon:"none"
						})
					}
				}
			})
		},
		gotoForgetPassword: function() {
			uni.navigateTo({
				url: 'forget-password'
			});
		}
	}
}
